.wrap {
  position: relative;
  overflow: hidden;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-brown {
  background: rgba(197,151,91, 1);
  box-shadow: 0px 9px 29px 0px rgba(197,151,91,0.72);
  span {
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
  }
  &:hover {
    box-shadow: 0px 9px 29px 10px rgba(197,151,91,0.72);
  }
}
.swiper-button-prev {
  z-index: 1;
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333333;
  &:after {
    display: none;
  }
  img {
    width: 8px;
  }
  &:hover {
    background-color: #0c4f3d;
  }
}
.swiper-button-next {
  z-index: 1;
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333333;
  &:after {
    display: none;
  }
  img {
    width: 8px;
  }
  &:hover {
    background-color: #0c4f3d;
  }
}
.swiper-button-disabled {
  opacity: 1 !important;
}
.swiper-pagination {
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  &-bullet {
    width: 8px;
    height: 8px;
    background-color: #dfdfdf;
    opacity: 1;
  }
  &-bullet-active {
    width: 16px;
    height: 16px;
    background: #ffffff;
    border: 3px solid #0c4f3d;
  }
}
.display2 {
  span {
    display: block;
    line-height: 120%;
    font-weight: 800;
    text-transform: uppercase;
    color: #333333;
  }
  span:nth-child(1) {
    font-size: 53px;
    opacity: 0.07;
  }
  span:nth-child(2) {
    font-size: 38px;
    margin-top: -25px;
  }
}
.tabs {
  &-head {
    display: flex;
    align-items: center;
    overflow-x: auto;
    padding-bottom: 10px;
    &::-webkit-scrollbar {
      height: 0px;
    }
    &::-webkit-scrollbar-track {
      background: #000000;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #0c4f3d;
      border-radius: 3px;
    }
  }
  .tab {
    flex-shrink: 0;
    min-width: 175px;
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #0c4f3d;
    border: 1px solid #0c4f3d;
    border-radius: 25px;
    padding: 5px 32px;
    transition: all 0.5s;
    cursor: pointer;
    &--active {
      background: #0c4f3d;
      color: #ffffff;
    }
    &:not(:first-child) {
      margin-left: 10px;
    }
    &:hover {
      background: #0c4f3d;
      color: #ffffff;
    }
  }
  &-content {
    display: none;
    &--active {
      display: block;
    }
  }
}
.btn-green {
  background: transparent;
  border: 1px solid #0c4f3d;
  border-radius: 25px;
  span {
    line-height: 120%;
    font-weight: 700;
    color: #0c4f3d;
    transition: all 0.5s;
  }
  &:hover {
    background: #0c4f3d;
    span {
      color: #ffffff;
    }
  }
}
.favorite {
  svg {
    transition: all 0.3s;
  }
  &--active {
    svg path {
      fill: #c5975b;
      stroke: #c5975b;
    }
  }
}
.up {
  position: fixed;
  z-index: 5;
  right: 20px;
  bottom: 100px;
  width: 55px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #0c4f3d;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s;
  img {
    width: 13px;
  }
  &--active {
    opacity: 1;
    pointer-events: all;
  }
  &:hover {
    background: #0e8665;
  }
}
.navigation {
  padding: 50px 0;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    li {
      a {
        font-size: 24px;
        line-height: 120%;
        color: #000000;
      }
      a:hover {
        color: #0c4f3d;
      }
    }
  }
}
.modal {
  background: rgba(#000, .4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 1350px;
    max-width: 100%;
    position: relative;
    border-radius: 15px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__dialog2 {
    width: 485px !important;
    background: #ffffff;
    border-radius: 6px;
    padding: 50px;
  }
  &__close{
    position: absolute;
    top: -25px;
    right: 0;
    width: 20px;
    background-color: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  &__close2{
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
    background-color: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__content2 {
    text-align: center;
  }
  &__left {
    width: 342px;
    background: #ffffff;
    box-shadow: 0px 9px 29px 0px rgba(0,0,0,0.05);
    border-radius: 15px 0px 0px 15px;
  }
  &__right {
    width: calc(100% - 342px);
    min-height: 690px;
  }
  &__top {
    background: #0c4f3d;
    border-radius: 15px 0 0 0;
    padding: 30px;
  }
  &__title {
    font-size: 21px;
    line-height: 120%;
    font-weight: 800;
    color: #ffffff;
  }
  &__title2 {
    font-size: 32px;
    line-height: 120%;
    font-weight: 700;
    color: #333333;
  }
  &__tabs-head {
    display: inline-flex;
    padding: 0;
    background: #ffffff;
    border-radius: 8px;
    overflow: unset;
    margin-top: 12px;
  }
  &__tab {
    width: 135px !important;
    height: 40px !important;
    min-width: auto !important;
    background: #ffffff !important;
    border: unset !important;
    border-radius: 6px !important;
    padding: 0 !important;
    text-transform: unset !important;
    color: #666666 !important;
    &:not(:first-child) {
      margin-left: 0 !important;
    }
  }
  .tab--active {
    background: #333333 !important;
    color: #ffffff !important;
  }
  &__items {
    height: 550px;
    display: grid;
    grid-template-columns: 100%;
    padding: 0 30px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #e5e5e5;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #0c4f3d;
      border-radius: 3px;
    }
  }
  &__item {
    padding: 30px 0;
    position: relative;
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        left: -30px;
        bottom: 0;
        width: calc(100% + 60px);
        height: 1px;
        background: rgba(0, 0, 0, 0.2);
      }
    }
    &-title {
      font-size: 16px;
      line-height: 120%;
      font-weight: 800;
      color: #333333;
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 15px;
    }
    &-group {
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        width: 12px;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 14px;
        line-height: 120%;
        font-weight: 500;
        color: #333333;
        margin-left: 5px;
        transition: all 0.3s;
      }
    }
    .location,
    .mail,
    .tel {
      &:hover {
        span {
          color: #0c4f3d;
        }
      }
    }
    &-btn {
      width: 163px;
      height: 37px;
      border: 1px solid #0c4f3d;
      border-radius: 17.6px;
      margin-top: 20px;
    }
  }
  .map{
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
    .ymaps-2-1-79-map ymaps {
      border-radius: 0 15px 15px 0;
    }
  }
  &__subtitle {
    font-size: 15px;
    line-height: 120%;
    color: #333333;
    margin-top: 15px;
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
    &-group {
      margin: 0;
      padding: 0;
      border: none;
      input {
        width: 100%;
        height: 55px;
        border: 1px solid #ececec;
        border-radius: 26.5px;
        font-size: 15px;
        line-height: 120%;
        font-weight: 500;
        color: #999999;
        padding: 5px 20px;
      }
    }
    &-btn {
      width: 210px;
      height: 55px;
      background: #0c4f3d;
      border: 1px solid #0c4f3d;
      border-radius: 27.6px;
      margin: 0 auto;
      span {
        font-size: 15px;
        line-height: 120%;
        font-weight: 700;
        color: #ffffff;
      }
      &:hover {
        background: #0e8665;
        border-color: #0e8665;
      }
    }
    &-info {
      font-size: 13px;
      line-height: 140%;
      color: #999999;
      a {
        text-decoration: underline;
        text-underline-offset: 3px;
        color: #0c4f3d;
      }
    }
  }
  &__icon {
    width: 38px;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &__btn {
    width: 210px;
    height: 55px;
    background: #0c4f3d;
    border: 1px solid #0c4f3d;
    border-radius: 27.6px;
    margin: 20px auto 0;
    font-size: 15px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    span {
      margin-left: 3px;
    }
    &:hover {
      background: #0e8665;
      border-color: #0e8665;
    }
  }
}
.account {
  &-title {
    font-size: 28px;
    line-height: 120%;
    font-weight: 700;
    color: #333333;
  }
  &-form {
    margin-top: 20px;
    &__group {
      margin: 0;
      padding: 0;
      border: none;
      &:not(:first-child) {
        margin-top: 10px;
      }
      input {
        width: 100%;
        height: 55px;
        background: transparent;
        border: 1px solid #ececec;
        border-radius: 6px;
        padding: 5px 20px;
      }
    }
  }
  &__info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }
  &__check {
    display: flex;
    align-items: center;
    position: relative;
    margin-right: 20px;
    margin-bottom: 10px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label {
        color: #0c4f3d;
        &:after {
          border-color: #0c4f3d;
        }
        &:before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    label {
      font-size: 13px;
      line-height: 120%;
      color: #333333;
      padding-left: 28px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 20px;
        height: 20px;
        border: 1px solid #ececec;
        border-radius: 4px;
        transition: all 0.3s;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translate(0, -50%);
        width: 11px;
        height: 7px;
        background-image: url("../img/check.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &__password {
    font-size: 13px;
    line-height: 120%;
    color: #999999;
    margin-bottom: 10px;
    &:hover {
      text-decoration: underline;
      text-underline-offset: 3px;
      color: #0c4f3d;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }
  &__enter {
    width: 135px;
    height: 43px;
    background: #0c4f3d;
    border: 1px solid #0c4f3d;
    border-radius: 6px;
    margin-right: 20px;
    margin-bottom: 10px;
    span {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
    }
    &:hover {
      background: #0e8665;
      border-color: #0e8665;
    }
  }
  &__registration {
    width: 162px;
    height: 45px;
    border-radius: 6px;
    margin-bottom: 10px;
    span {
      font-size: 14px;
    }
  }
  &__socials {
    margin-top: 20px;
    &-title {
      font-size: 15px;
      line-height: 120%;
      color: #999999;
    }
    &-group {
      display: flex;
      align-items: center;
      margin-top: 15px;
    }
  }
  &__social {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #333333;
   border-radius: 6px;
    &:not(:first-child) {
      margin-left: 8px;
    }
    img {
      width: 14px;
    }
  }
}
.menu {
  position: fixed;
  z-index: 10;
  top: 0;
  left: -105%;
  width: 300px;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-image: url("../img/menu-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: all 0.5s;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 0;
  }
  &--active {
    left: 0;
  }
  &__top {
    background: #313131;
    padding: 15px;
  }
  &__bottom {
    flex-grow: 1;
    background: #313131;
    padding: 15px;
  }
  &__logo {
    display: block;
    width: 215px;
    img {
      width: 100%;
    }
  }
  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 15px;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  &__items {
    height: 452px;
    display: grid;
    grid-template-columns: 100%;
    overflow-y: auto;
    position: relative;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #e5e5e5;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #0c4f3d;
      border-radius: 3px;
    }
  }
  &__item {
    &:not(:last-child) {
      border-bottom: 1px solid #eae4d7;
    }
    &:first-child {
      .menu__item-hidden {
        padding: 0;
      }
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 15px;
      cursor: pointer;
      span {
        font-size: 16px;
        line-height: 120%;
        font-weight: 600;
        color: #333333;
      }
      i {
        width: 8px;
        transform: rotate(-180deg);
        transition: all 0.5s;
        svg {
          width: 100%;
          height: auto;
        }
        svg path {
          fill: #333333;
        }
      }
    }
    &-title {
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: #333333;
      svg {
        width: 18px;
        margin-right: 5px;
      }
    }
    &-hidden {
      padding: 15px;
      display: none;
    }
    &-link {
      display: block;
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: #333333;
      &:not(:first-child) {
        margin-top: 15px;
      }
      &:hover {
        color: #0c4f3d;
      }
    }
  }
  &__img {
    margin-top: 30px;
    img {
      display: block;
      width: 100%;
    }
  }
  .item-show {
    transition: all 0.5s;
    &:hover {
      background: #0c4f3d;
      filter: drop-shadow(0px 9px 14.5px rgba(0,0,0,0.05));
      span {
        color: #ffffff;
      }
      i {
        svg {
          path {
            fill: #ffffff;
          }
        }
      }
      .menu__item-title {
        color: #ffffff;
        svg {
          path {
            fill: #ffffff;
          }
          rect {
            fill: #ffffff;
          }
        }
      }
    }
  }
  &__bottom-items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__bottom-item {
    display: flex;
    align-items: center;
    i {
      width: 14px;
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: #fff;
      margin-left: 10px;
    }
  }
  .header-top__tel {
    margin-left: 0;
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-top: 15px;
  }
  &__social {
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 2px;
    background-color: rgba(255,255,255,0);
    border: 1px solid #454545;
    img {
      max-width: 20px;
    }
    &:not(:first-child) {
      margin-left: 10px;
    }
  }
}
.bg-menu {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(#000, .4);
  transition: all 0.5s;
  opacity: 0;
  pointer-events: none;
  &--active {
    opacity: 1;
  }
}
.catalog {
  &-item {
    &:not(:last-child) {
      border-bottom: 1px solid #f1f1f1;
    }
    &__show {
      display: flex;
      justify-content: space-between;
      align-items: center;
