      }
    }
    &__list {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 15px;
      li {
        font-size: 15px;
        line-height: 120%;
        color: #666666;
      }
    }
  }
}
.mob-menu {
  position: fixed;
  z-index: 3;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #ececec;
  filter: drop-shadow(0 0 13.5px rgba(0,0,0,0.17));
  padding: 20px 15px;
  display: none;
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__btn {
    display: flex;
    flex-direction: column;
    background: transparent;
    padding: 0;
    &:not(:first-child) {
      margin-left: 10px;
    }
    &-icon {
      width: 20px;
      height: 22px;
      margin: 0 auto;
      position: relative;
      span {
        position: absolute;
        top: -12px;
        right: -12px;
        width: 17px;
        height: 17px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgba(197,151,91,1);
        border-radius: 50%;
        font-size: 9px;
        line-height: 120%;
        font-weight: 700;
        color: #ffffff;
      }
      img {
        width: 100%;
      }
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-title {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: #333333;
      text-align: center;
      margin-top: 5px;
    }
  }
}
.mob-catalog {
  position: fixed;
  z-index: 3;
  top: 0;
  left: -105%;
  width: 300px;
  height: 100%;
  background-image: url("../img/menu-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: all 0.5s;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 3px;
  }
  &::-webkit-scrollbar-track {
    background: #e5e5e5;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #0c4f3d;
    border-radius: 3px;
  }
  &--active {
    left: 0;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    padding: 15px;
  }
  &__title {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
  }
  &__close {
    width: 28px;
    margin-left: 10px;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1500px) {
  .modal {
    &__dialog {
      width: 900px;
    }
  }
}

@media screen and (max-width: 992px) {
  .wrap {
    padding-bottom: 91px;
  }
  .display2 {
    span:nth-child(1) {
      font-size: 32px;
    }
    span:nth-child(2) {
      font-size: 28px;
      margin-top: -15px;
    }
  }
  .tabs {
    .tab {
      min-width: 95px;
      height: 34px;
      font-size: 13px;
      padding: 3px 20px;
      &:not(:first-child) {
        margin-left: 7px;
      }
    }
  }
  .modal {
    &__content {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      height: 400px;
      min-height: auto;
    }
  }
  .menu {
    .header-top__tel {
      display: flex;
    }
    .header-top__location {
      display: flex;
    }
  }
  .mob-menu {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .modal {
    &__dialog2 {
      width: 100% !important;
      padding: 30px 20px;
    }
    &__title2 {
      font-size: 26px;
    }
    &__tab {
      width: 100px !important;
      height: 35px !important;
    }
    &__subtitle {
      font-size: 13px;
    }
    &__form {

    }
  }
  .account {
    &__check {
      margin-right: 10px;
    }
    &__enter {
      width: 109px;
      margin-right: 10px;
    }
    &__registration {
      width: 130px;
    }
  }
  .menu {
    width: 100%;
  }
  .mob-menu {
    &__btn {
      &:not(:first-child) {
        margin-left: 5px;
      }
      &-icon {
        height: 18px;
        img,
        svg {
          width: 15px;
        }
      }
      &-title {
        font-size: 11px;
      }
    }
  }
  .mob-catalog {
    width: 100%;
  }
}