.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
}
.header-top {
  background: #333;
  padding: 15px 0;
  .container {
    max-width: 1900px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__center {
    margin: 0 15px;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__burger {
    position: relative;
    width: 22px;
    height: 20px;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      height: 2px;
      background: #ffffff;
    }
    span:nth-child(1) {
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 15px;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
      width: 100%;
    }
    span:nth-child(3) {
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 15px;
    }
  }
  &__tel {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-left: 230px;
    i {
      width: 14px;
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
      margin: 0 10px;
    }
  }
  &__logo {
    display: block;
    width: 250px;
    img {
      width: 100%;
    }
  }
  &__location {
    display: flex;
    align-items: center;
    cursor: pointer;
    i {
      width: 14px;
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
      margin: 0 10px;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-left: 185px;
  }
  &__search {
    position: relative;
    width: 22px;
    height: 52px;
    input {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 0;
      height: 100%;
      border: 1px solid #ededed;
      box-shadow: 0px 9px 29px 0px rgba(0,0,0,0.05);
      border-radius: 6px 6px 0px 0px;
      padding: 0;
      font-size: 16px;
      line-height: 120%;
      color: #333333;
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
    }
    .search-button {
      position: absolute;
      z-index: 1;
      width: 22px;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      background: transparent;
      padding: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
    .search-results {
      position: absolute;
      z-index: 2;
      top: 100%;
      right: 0;
      width: 430px;
      background-color: #f8f8f8;
      border-radius: 0 0 8px 8px;
      padding: 15px 0 60px 25px;
      opacity: 0;
      pointer-events: none;
      &--active {
        opacity: 1;
        pointer-events: all;
      }
      &__items {
        height: 300px;
        display: grid;
        grid-template-columns: 100%;
        overflow-y: auto;
        padding-right: 15px;
        &::-webkit-scrollbar {
          width: 3px;
        }
        &::-webkit-scrollbar-track {
          background: #e5e5e5;
          border-radius: 3px;
        }
        &::-webkit-scrollbar-thumb {
          background: #0c4f3d;
          border-radius: 3px;
        }
      }
      &__item {
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        &:not(:last-child) {
          border-bottom: 1px solid #ededed;
          padding-bottom: 15px;
        }
        &:hover {
          .search-results__item-title {
            color: #0c4f3d;
            text-decoration: underline;
            text-underline-offset: 3px;
          }
        }
        &-img {
          flex-shrink: 0;
          width: 70px;
          height: 70px;
          display: flex;
          justify-content: center;
          align-items: center;
          border: 1px solid #ededed;
          box-shadow: 0px 9px 29px 0px rgba(0,0,0,0.05);
          border-radius: 6px;
        }
        &-desc {
          margin-left: 15px;
        }
        &-title {
          font-size: 16px;
          line-height: 120%;
          font-weight: 500;
          color: #333333;
          transition: all 0.3s;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp:2;
          -webkit-box-orient: vertical;
        }
        &-info {
          font-size: 13px;
          line-height: 120%;
          font-weight: 500;
          color: #999999;
          margin-top: 10px;
        }
      }
      &__quantity {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #ffffff;
        border: 1px solid #ededed;
        box-shadow: 0px 9px 29px 0px rgba(0,0,0,0.05);
        border-radius: 0px 0px 6px 6px;
        padding: 20px 5px;
        &-text {
          font-size: 16px;
          line-height: 120%;
          font-weight: 600;
          text-decoration: underline;
          text-underline-offset: 3px;
          color: #0c4f3d;
          &:hover {
            text-decoration: unset;
          }
        }
      }
    }
    &--active {
      input {
        width: 430px;
        padding: 5px 40px 5px 20px;
        opacity: 1;
        pointer-events: all;
      }
      .search-button {
        right: 15px;
        svg path {
          stroke: #0c4f3d;
          fill: #0c4f3d;;
        }
      }
    }
  }
  &__user {
    width: 22px;
    cursor: pointer;
    margin-left: 24px;
    img {
      width: 100%;
    }
  }
  &__location-mob {
    width: 22px;
    cursor: pointer;
    margin-left: 24px;
    display: none;
    img {
      width: 100%;
    }
  }
}
.header-bottom {
  background-color: rgba(249,249,249,0.9);
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__list {
    display: flex;
    align-items: center;
    li:not(:last-child) {
      margin-right: 20px;
    }
    li:first-child {
      margin-right: 0;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    a {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: #333333;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: -10px;
        bottom: -10px;
        width: 0;
        height: 1px;
        background: #0c4f3d;
        transition: all 0.5s;
      }
      &:hover {
        color: #0c4f3d;
        &:after {
          width: calc(100% + 20px);
        }
      }
    }
    span {
      display: block;
      width: 1px;
      height: 42px;
      background: rgba(51, 51, 51, 0.1);
      margin-right: 20px;
    }
  }
  &__catalog {
    position: relative;
    z-index: 3;
    &:hover {
      .header-bottom__catalog-hidden {
        opacity: 1;
        pointer-events: all;
      }
      .header-bottom__catalog-show {
        &:after {
          opacity: 1;
          pointer-events: all;
        }
      }
      .header-bottom__catalog-title {
        i svg {
          path {
            fill: #ffffff;
          }
          rect {
            fill: #ffffff;
          }
        }
        span {
          color: #ffffff;
        }
      }
    }
    &-show {
      cursor: pointer;
      padding: 30px 15px 30px 15px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #0c4f3d;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
    &-title {
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        width: 18px;
        svg {
          width: 100%;
          height: auto;
        }
      }
      span {
        font-size: 16px;
        line-height: 120%;
        font-weight: 700;
        color: #333333;
        margin-left: 10px;
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      width: 300px;
      background-color: #ffffff;
      filter: drop-shadow(0px 12px 13.5px rgba(0,0,0,0.02));
      padding: 5px 0;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-left: 30px;
  }
  &__btn {
    width: 22px;
    background: transparent;
    padding: 0;
    position: relative;
    &:not(:first-child) {
      margin-left: 20px;
    }
    &:hover {
      svg {
        stroke: #0c4f3d;
        .cls-1 {
          stroke: #0c4f3d;
        }
        path {
          stroke: #0c4f3d;
        }
      }
    }
    svg {
      width: 100%;
      height: auto;
      defs {
        transition: all 0.3s;
      }
    }
    span {
      position: absolute;
      top: -12px;
      right: -12px;
      width: 17px;
      height: 17px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(197,151,91,1);
      border-radius: 50%;
      font-size: 9px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
    }
  }
}
.catalog2 {
  &-items {
    height: 700px;
    overflow-x: hidden;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #e5e5e5;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #0c4f3d;
      border-radius: 3px;
    }
  }
  &__item {
    &:not(:last-child) {
      border-bottom: 1px solid #f9f9f9;
    }
  }
  &__show {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    padding: 15px;
    position: relative;
    i {
      transform: rotate(-180deg);
      width: 8px;
      transition: all 0.3s;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 4px;
      height: 100%;
      background: #0c4f3d;
      opacity: 0;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 2;
      top: 0;
      right: -1050px;
      width: 4px;
      height: 100%;
      background: #0c4f3d;
      opacity: 0;
      pointer-events: none;
    }
    &--active {
      background-color: #f3fbf9;
      &:after {
        opacity: 1;
        pointer-events: all;
      }
      &:before {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
  &__title {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
    color: #333333;
    img {
      width: 22px;
      margin-right: 10px;
    }
  }
  &__hidden {
    position: absolute;
    top: 0;
    left: 100%;
    width: 1050px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    background: #ffffff;
    padding: 20px 30px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__hidden-left {
    width: 50%;
    margin-right: 50px;
  }
  &__hidden-right {
    width: 50%;
  }
  &__wrap {
    background: #f6f5f5;
    border: 1px solid #f6f5f5;
    border-radius: 20px;
    padding-bottom: 22px;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 22px;
    span {
      font-size: 16px;
      line-height: 25px;
      font-weight: 700;
      color: #333333;
    }
    i {
      flex-shrink: 0;
      width: 8px;
      margin-left: 5px;
      transform: rotate(-180deg);
    }
    &:hover {
      span {
        color: #0c4f3d;
      }
      i svg path {
        fill: #0c4f3d;
      }
    }
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 100px / 2));
    gap: 45px 100px;
  }
  &__nav {
    &2 {
      grid-row: 1/3;
      grid-column: 3/2;
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
      color: #333333;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
    li {
      &:not(:last-child) {
        margin-right: 0;
      }
      a {
        font-size: 15px;
        line-height: 120%;
        color: #666666;
      }
      a:hover {
        text-decoration: underline;
        text-underline-offset: 3px;
        color: #0c4f3d;
      }
    }
  }
}
.header-fixed {
  position: fixed;
  top: -105%;
  left: 0;
  right: 0;
  width: 100%;
  background: #ffffff;
  box-shadow: 4px 4px 29px 0px rgba(34, 60, 80, 0.2);
  transition: all 0.5s;
  &--active {
    top: 0;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__center {
    margin: 0 15px;
  }
  .header-top__burger {
    span {
      background: #333333;
    }
  }
  .header-bottom__btns {
    margin-left: 0;
  }
  .header-bottom__btn {
    &:not(:first-child) {
      margin-left: 15px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .header-top {
    &__tel {
      margin-left: 24px;
    }
    &__btns {
      margin-left: 24px;
    }
  }
}

@media screen and (max-width: 1450px) {
  .catalog2 {
    &__show {
      &:before {
        right: -650px;
      }
    }
    &__hidden {
      width: 650px;
    }
    &__hidden-left {
      margin-right: 30px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header-bottom {
    &__list {
      li:not(:last-child) {
        margin-right: 15px;
      }
      li:first-child {
        margin-right: 0 ;
      }
    }
    &__catalog {
      &-show {
        padding: 25px 15px 25px 0;
        &:after {
          left: -15px;
          width: calc(100% + 15px);
        }
      }
    }
    &__btns {
      margin-left: 15px;
    }
  }
}

@media screen and (max-width: 1250px) {
  .header-bottom {
    &__link {
      span {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .header-fixed {
    .header-bottom__catalog-title {
      span {
        font-size: 15px;
      }
    }
    .header-bottom__link a {
      font-size: 15px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header-top {
    &__center {
      margin: 0 15px;
    }
    &__tel {
      display: none;
    }
    &__logo {
      width: 180px;
    }
    &__location {
      display: none;
    }
    &__btns {
      margin-left: 0;
    }
    &__user {
      display: none;
    }
    &__location-mob {
      display: block;
    }
  }
  .header-bottom {
    display: none;
  }
  .header-fixed {
    display: none;
  }
}

@media screen and (max-width: 576px) {
  .header-top {
    &__search {
      input {
        right: -46px;
        height: 40px;
      }
      .search-button {
        right: 5px;
      }
      &--active {
        input {
          width: 300px;
          padding: 5px 30px 5px 10px;
        }
        .search-button {
          right: -40px;
        }
      }
      .search-results {
        right: -46px;
        width: 300px;
      }
    }
    &__location-mob {
      margin-left: 15px;
    }
  }
}

@media screen and (max-width: 360px) {
  .header-top {
    &__logo {
      width: 150px;
    }
  }
}