.footer-top {
  background: #333333;
  padding: 70px 0 40px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    margin-bottom: 30px;
    &:not(:last-child) {
      margin-right: 30px;
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #3f3f3f;
      padding-bottom: 15px;
      cursor: pointer;
    }
    &-hidden {
      padding-top: 15px;
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      font-weight: 800;
      text-transform: uppercase;
      color: #ffffff;
    }
    &-arrow {
      display: none;
      flex-shrink: 0;
      width: 16px;
      margin-left: 10px;
      img {
        width: 100%;
      }
    }
    &-link {
      display: block;
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 15px;
      }
      &:hover {
        text-decoration: underline;
        text-underline-offset: 3px;
        color: #c5975b;
      }
    }
    &-wrap {
      display: inline-flex;
      flex-direction: column;
    }
  }
  .header-top__tel {
    margin-left: 0;
  }
  .header-top__location {
    margin-top: 20px;
  }
  .email {
    display: flex;
    align-items: center;
    margin-top: 20px;
    &:hover {
      span {
        color: #c5975b;
      }
    }
    i {
      flex-shrink: 0;
      width: 14px;
    }
    span {
      font-size: 17px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
      margin-left: 10px;
      transition: all 0.5s;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
  &__social {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #454545;
    border-radius: 2px;
    &:not(:first-child) {
      margin-left: 10px;
    }
    img {
      max-width: 20px;
      width: auto;
    }
  }
  .vk {
    background-color: #2787f5;
  }
  .whatsapp {
    background-color: #29a71a;
  }
}
.footer-bottom {
  background: #303030;
  padding: 20px 0 5px;
  &__flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__item {
    margin-bottom: 15px;
    &:not(:last-child) {
      margin-right: 30px;
    }
  }
  &__text {
    font-size: 15px;
    line-height: 140%;
    color: #ffffff;
  }
  &__links {
    display: flex;
    align-items: center;
  }
  &__link {
    font-size: 15px;
    line-height: 140%;
    text-decoration: underline;
    text-underline-offset: 3px;
    color: #ffffff;
    &:not(:first-child) {
      margin-left: 30px;
    }
  }
  &__img {
    width: 767px;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__logo {
    display: block;
    width: 157px;
    img {
      width: 100%;
    }
  }
}
.footer-mob {
  display: none;
  background: #303030;
  padding: 20px 0 30px;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    text-align: center;
  }
}

@media screen and (max-width: 992px) {
  .footer-top {
    .header-top__tel {
      display: flex;
    }
    .header-top__location {
      display: flex;
    }
  }
  .footer-bottom {
    &__img {
      width: 600px;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-bottom {
    &__img {
      width: 260px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    padding: 40px 0;
    &__items {
      display: block;
    }
    &__item {
      margin-bottom: 0;
      &:not(:last-child) {
        margin-bottom: 30px;
        margin-right: 0;
      }
      &-arrow {
        display: block;
      }
      &-hidden {
        display: none;
      }
      &-wrap {
        width: 100%;
        align-items: center;
      }
    }
    &__social {
      background-color: transparent !important;
    }
  }
  .footer-bottom {
    display: none;
    padding: 20px 0;
    &__inner {
      text-align: center;
    }
    &__flex {
      display: block;
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
    &__item {
      margin-bottom: 0;
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
    &__links {
      flex-direction: column;
    }
    &__link {
      &:not(:first-child) {
        margin: 15px 0 0 0;
      }
    }
    &__img {
      margin: 0 auto;
    }
    &__logo {
      margin: 0 auto;
    }
  }
  .footer-mob {
    display: block;
  }
}