.main {
  position: relative;
  &-bg {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: #f6f6f6;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__slider {
    position: relative;
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  &-slide {
    height: auto;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    text-align: center;
    padding: 350px 0 150px;
    &.swiper-slide-active {
      .main-slide__img img {
        animation-name: shake;
        animation-duration: 7s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      }
    }
  }
  &-slide__img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    img {
      width: 105%;
      height: 100%;
    }
  }
  &-slide__wrap {
    position: relative;
  }
  &-slide__desc {
    position: relative;
    z-index: 2;
  }
  &__title {
    max-width: 1080px;
    font-size: 65px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    text-shadow: 0px 12px 9px rgba(0,0,0,0.09);
    color: #333333;
    margin: 0 auto;
    .green-text {
      display: block;
      font-size: 39px;
      line-height: 120%;
      font-weight: 800;
      color: #0c4f3d;
      text-align: right;
      margin-top: 10px;
      margin-right: 225px;
    }
  }
  &__text {
    max-width: 430px;
    font-size: 22px;
    line-height: 120%;
    color: #999999;
    margin: 30px auto 0;
  }
  &__btn {
    width: 294px;
    height: 61px;
    border-radius: 30px;
    margin: 30px auto 0;
    span {
      font-size: 18px;
    }
  }
  &__slider-btns {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin-top: -36px;
    margin-right: 30px;
  }
  .swiper-button-prev {
    position: static;
    margin-top: 0;
  }
  .swiper-button-next {
    position: static;
    margin-top: 0;
    margin-left: 4px;
  }
  .swiper-pagination {
    bottom: 70px;
  }
  &-img {
    position: absolute;
    top: -100px;
    left: 50%;
    transform: translate(-50%, 0);
    width: 245px;
  }
  &-img2 {
    position: absolute;
    left: 100px;
    bottom: 25px;
    width: 288px;
  }
  &-img3 {
    position: absolute;
    top: -90px;
    left: 38%;
    transform: translate(-38%, 0);
    width: 91px;
  }
  &-img4 {
    position: absolute;
    left: 30%;
    bottom: -30px;
    transform: translate(-30%, 0);
    width: 82px;
  }
  &-img5 {
    position: absolute;
    right: 150px;
    bottom: -50px;
    width: 250px;
  }
  &__arrow {
    position: absolute;
    z-index: 1;
    left: 50%;
    bottom: -30px;
    transform: translate(-50%, 0);
    width: 63px;
    height: 63px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f7f7f7;
    border: 5px solid #ffffff;
    border-radius: 50%;
    cursor: pointer;
    img {
      width: 13px;
    }
  }
}
.advantages {
  padding: 100px 0 50px;
  &__card {
    background-image: url("../img/advantages-card__bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 30px 30px 10px 10px;
    padding: 130px 15px;
    margin-top: 45px;
    position: relative;
    transition: background 0.5s;
    display: none;
    &--active {
      display: block;
    }
    &2 {
      background-image: url("../img/advantages-card__bg3.png");
    }
    &-desc {
      max-width: 680px;
      margin: 0 auto;
      text-align: center;
    }
    &-title {
      font-size: 34px;
      line-height: 150%;
      font-weight: 800;
      text-transform: uppercase;
      color: #333333;
    }
    &-text {
      font-size: 18px;
      line-height: 140%;
      font-weight: 500;
      margin-top: 20px;
    }
    &-link {
      display: block;
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
      color: #c5975b;
      margin-top: 20px;
    }
  }
  &__swiper {
    padding: 35px 0 0 0;
  }
  &__slider {
    position: relative;
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  &-slide {
    height: auto;
    background-color: #f6f6f6;
    border: 1px solid #f0f0f0;
    border-radius: 10px;
    padding: 40px 15px;
    text-align: center;
    position: relative;
    transition: all 0.5s;
    &:hover {
      background: #166453;
      &:after {
        opacity: 1;
      }
      &:before {
        opacity: 1;
      }
      .advantages-slide__icon {
        svg {
          path {
            fill: #ffffff;
          }
        }
      }
      .advantages-slide__text {
        color: #ffffff;
      }
    }
    &:after {
      content: '';
      position: absolute;
      top: -35px;
      left: 50%;
      transform: translate(-50%, 0);
      border: 21px solid transparent;
      border-bottom: 30px solid #166453;
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 10px;
      left: 50%;
      transform: translate(-50%, -0);
      width: 100px;
      height: 100px;
      background: #ffffff;
      border-radius: 50%;
      filter: blur(50px);
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
    }
    &__desc {
      position: relative;
      z-index: 2;
    }
    &__icon {
      max-width: 50px;
      margin: 0 auto;
      svg {
        max-width: 100%;
        height: auto;
        path {
          fill: #000;
          transition: all 0.3s;
        }
      }
    }
    &__text {
      font-size: 17px;
      line-height: 140%;
      font-weight: 600;
      color: #333333;
      margin-top: 15px;
      transition: all 0.3s;
    }
  }
  &__slider-btns {
    display: none;
  }
  .swiper-pagination {
    bottom: -45px;
  }
}
.catalog {
  background: #f6f6f6;
  padding: 50px 0;
  &__title {
    text-align: center;
  }
  &__tabs {
    margin-top: 35px;
    &-body {
      margin-top: 60px;
    }
  }
  &-cards {
    width: calc(100% + 30px);
    display: flex;
    flex-wrap: wrap;
  }
  &-card {
    width: calc(100% / 4 - 30px);
    display: flex;
    flex-direction: column;
    background: #ffffff;
    border: 1px solid #f8f8f8;
    border-radius: 10px;
    padding: 50px 15px;
    text-align: center;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    margin: 0 30px 30px 0;
    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: 57px;
      height: 57px;
      border-radius: 50%;
      background-color: #fbf0e6;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      box-shadow: 0px 9px 29px 0px rgba(0,0,0,0.05);
      &:after {
        opacity: 1;
        pointer-events: all;
      }
      .catalog-card__img {
        img {
          transform: scale(1.1);
        }
      }
      .catalog-card__arrow {
        background: #c5975b;
        img {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    &__img {
      max-width: 250px;
      margin: 0 auto auto;
      img {
        max-width: 100%;
        transition: all 0.3s;
      }
    }
    &__title {
      font-size: 20px;
      line-height: 120%;
      color: #666666;
      margin-top: 50px;
    }
    &__arrow {
      position: absolute;
      z-index: 1;
      right: -10px;
      bottom: -10px;
      width: 57px;
      height: 57px;
      border-radius: 50%;
      background-color: #fbf0e6;
      padding: 18px 5px 0 0;
      transition: all 0.3s;
      img {
        width: 25px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
}
