      }
    }
  }
  .pawnshop {
    background-image: url("../img/pawnshop-bg2.png");
    background-position: bottom;
    padding: 50px 0 330px;
    &__inner {
      text-align: center;
    }
    &__btn {
      width: 100%;
    }
  }
  .info {
    &__top {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
    &__title {
      margin: 0;
    }
    &__link {
      margin: 10px 0 0 0;
    }
    &__btn {
      display: flex;
    }
  }
}

@media screen and (max-width: 360px) {
  .jewelry {
    &__img {
      max-width: 200px;
